import React, { Component } from "react"
import styled from "styled-components"

const Base = styled.div`
    background-color: #000;
    width: 100%;
`

const BaseContent = styled.div`
`

const Square = styled.div`
    width: ${props => 100/props.width + "%"};
    display: inline-block;
    float: left;
    outline 2px solid gray;
    background-color: ${props => props.color};

    &:after {
        content: "";
        display: block;
        padding-bottom: 100%;
    }
`

const Row = styled.div`
    display: block;
`

export default class Matrix extends Component {
    constructor(props) {
        super(props)
        const defaultColor = "#000";

        let mat = [];
        for(var i = 0; i < this.props.height; i++) {
            let innerrow = [];
            for(var j = 0; j < this.props.width; j++) {
                innerrow.push(defaultColor);
            }
            mat.push(innerrow)
        }

        this.state = {
            width: this.props.width,
            height: this.props.height,
            mat: mat,
            dragging: false
        }
    }

    changeColor(i,j) {
            let mat = this.state.mat.slice()
            mat[i][j] = "#FFF";
            this.setState({mat: mat})
    }

    createRow(i) {
        let row = []
        for(let j = 0; j < this.state.width; j++) {
            row.push(<Square color={this.state.mat[i][j]} 
                onMouseOver={() => {if(this.state.dragging) {
                    this.changeColor(i,j)}}}
                onClick={() => {this.changeColor(i,j)}}
                onMouseDown={() => {this.setState({dragging: true})}}
                onMouseUp={() => {this.setState({dragging: false})}}
                width={this.state.width}/>)
        }
        return row
    }

    createMatrix() {
        let cols = [];
        for(let i = 0; i < this.state.height; i++) {
            cols.push(<Row>{this.createRow(i)}</Row>)
        }
        return cols
    }

    render() {

        return (
        <Base>
            <BaseContent>
                {this.createMatrix()}
            </BaseContent>
        </Base>
        )
    }
}
